.dropdown-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.dropdown-label {
  font-size: 18px;
  margin-bottom: 10px;
}

.dropdown-select {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
}

.selected-option {
  margin-top: 10px;
  font-size: 16px;
}
